import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import Title from './component/Title';

class App extends Component {
  render() {
    return (
      <div>
        <nav>
          <div className="nav-wrapper black">
              <img src={logo} className="logo" alt="Logo"></img>
          </div>
        </nav>

          <Title />
      </div >
        );
      }
    };
    
    export default App;



