import React, { Component } from 'react';
import { Row, Col, Card, Input, Button } from 'react-materialize';
import '../App.css';

import Weather from './Weather';

const API_KEY = "816a528d2d5de10c792332aadfd8257f";

class Title extends Component {
    state = {
        city: undefined,
        country: undefined,
        temp: undefined,
        humidity: undefined,
        error: undefined
    }

    getWeather = async (e) => {
        e.preventDefault();
        const city = e.target.elements.city.value;
        const country = e.target.elements.country.value;


        if (city && country) {
            const api_call = await fetch(`http://api.openweathermap.org/data/2.5/weather?q=${city},${country}&APPID=${API_KEY}&units=metric`);
            const data = await api_call.json();
            if (data.sys) {
                this.setState({
                    city: data.name,
                    country: data.sys.country,
                    temp: data.main.temp,
                    humidity: data.main.humidity,
                    error: ""
                })
            }
            else {
                this.setState({
                    city: undefined,
                    country: undefined,
                    temp: undefined,
                    humidity: undefined,
                    error: "Cant get location :( "
                })
            }
        }
    }
        render() {
            return (
                <div className="container">
                    <Row>
                        <Col s={12} m={6}>
                            <Card className="grey darken-3" textClassName="white-text">
                                <span className="card-title">Please Enter City and Country to search </span>
                                <form className="white-text" onSubmit={this.getWeather} autoComplete="off">
                                    <Row>
                                        <Input s={12} label="City" name="city" />
                                    </Row>
                                    <Row>
                                        <Input s={12} label="Country" name="country" />
                                    </Row>
                                    <Button className="yellow black-text" waves="light">GET WEATHER </Button>
                                </form>
                            </Card>
                        </Col>


                        <Col s={12} m={6}>
                            <Weather
                                city={this.state.city}
                                country={this.state.country}
                                temp={this.state.temp}
                                humidity={this.state.humidity}
                                error={this.state.error}
                            />
                        </Col>
                    </Row>
                </div >
            );
        }
    };

    export default Title;


