import React from 'react';
import sun from '../sun.png';
import '../App.css';

const Weather = (props) => (
    <div>
        {props.city && props.country &&
            <div className="card grey darken-3">
                <div className="card-image">
                    <img src={sun} alt="sun"></img>
                </div>
                <div className="card-content white-text">
                    <span className="card-title">Location : {props.city}, {props.country}</span>
                    {props.temp &&
                        <span className="card-title">Temperature : {props.temp} C</span>
                    }
                    {props.humidity &&
                        <span className="card-title">Humidity : {props.humidity}</span>
                    }
                </div>
            </div>
        }
        {props.error &&
            <div> <p> {props.error} </p> </div>
        }
    </div>
);

export default Weather;


